import React, { useState } from "react";
import { Clock } from "../Clock";
import { FormBox, FormFieldset, Legend, FormLabel, FormInput, FormButton, Text } from "./styled";

export const Form = ({ calculateResult, ratesData }) => {
  const [amount, setAmount] = useState("");
  const [sourceCurrency, setSourceCurrency] = useState("GBP");

  const onSubmit = (event) => {
    event.preventDefault();
    calculateResult(+amount, sourceCurrency);
  }
  const options = ratesData
    && Object.keys(ratesData.currencies).map(currency =>
      <option key={currency}>{currency}</option>);

  return (
    <FormBox onSubmit={onSubmit}>
      <Clock />
      <FormFieldset>

        <Legend>UzupeÅ‚nij dane:</Legend>
        {ratesData
          ? (<>
            <p>
              <FormLabel htmlFor="amount" className="form__label">Podaj kwotÄ™ w zÅ‚otÃ³wkach*:</FormLabel>
              <FormInput
                value={amount}
                onChange={({ target }) => setAmount(target.value)}
                type="number" id="amount"
                name="amount"
                required
              />
            </p>
            <p>
              <FormLabel htmlFor="currency" className="form__label">Wybierz walutÄ™:</FormLabel>
              <FormInput as="select"
                value={sourceCurrency}
                onChange={({ target }) => setSourceCurrency(target.value)}
                name="currency"
                id="currency">
                {options}
              </FormInput>
            </p>
            <p>
              <FormButton>Przelicz</FormButton>
            </p>
          </>)
          : (<Text>
            {ratesData === "error"
              ? `WystÄ…piÅ‚ bÅ‚Ä…d. SprawdÅº poÅ‚Ä…czenie internetowe,
              a jeÅ›li to nie pomoÅ¼e - sprÃ³buj ponownie pÃ³Åºniej.`
              :
              `Trwa pobieranie danych z Europejskiego Banku Centralnego.`
            }
          </Text>)
        }
      </FormFieldset>
    </FormBox>
  )

}